import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import{catchError} from 'rxjs/operators';
import { throwError } from 'rxjs';

@Injectable({providedIn:'root'})


export class SignupService{
    baseUrl:string = "http://localhost:3000";
    constructor(private http : HttpClient){

    }

    onCreate(postData:{email:string,password:string}){
        return this.http.
        post("https://ng-complete-guide-787a8.firebaseio.com/post.json",postData).pipe(catchError(this.errorHandler))
    }

    onFetch(){
        return this.http.get("https://my-json-server.typicode.com/angelmathew07/data/db").pipe(catchError(this.errorHandlerfForGet))
    }
    onFetchPromise(){

        return new Promise(function(resolve, reject) {

            //Open a new XHR
            var request = new XMLHttpRequest();
            request.open('GET', "https://my-json-server.typicode.com/angelmathew07/data/db");
        
            // When the request loads, check whether it was successful
            request.onload = function() {
              if (request.status === 200) {
                // If successful, resolve the promise
                resolve(request.response);
              } else {
                // Otherwise, reject the promise
                reject(Error('An error occurred while loading image. error code:' + request.statusText));
              }
            };
        
            request.send();
          });



    }

    onSearch(name:string){
        return this.http.get("https://my-json-server.typicode.com/angelmathew07/data/post")
    }

    private errorHandler(errorResponse:HttpErrorResponse){
        let errorMessage="An unknown error";
        console.log(errorResponse)
        if(!errorResponse.error.error || !errorResponse.error)
        {
            return throwError(errorMessage)
        }
        else{
            errorMessage= errorResponse.error.error
            return throwError(errorMessage)
        }
    }

    private errorHandlerfForGet(errorResp:HttpErrorResponse){
        let errorMessage="Unknown error"
        if(errorResp.message){
            errorMessage=errorResp.message
            return throwError(errorMessage)
        }
        return throwError(errorMessage)
    }

    }
   