import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sort1'
})
export class Sort1Pipe implements PipeTransform {

  transform(value: any,name:string): any {
    return value.sort((a,b)=>
    {
      if(a[name]>b[name]){
        return 1
      }
      else{
        return -1
      }
    });
  }

}
