import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sort'
})
export class SortPipe implements PipeTransform {

  transform(value: any, username: string): any {
    return value.sort((a,b)=>{
      if(a[username]>b[username]){
        return 1
      }
      else{
        return -1
      }
    });
    
  }

}
