import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {

  transform(value: any, sex:string): any {
    const returnArray=[]
    if(value.length===0 || sex=='' || sex==null){
return value
    }
    for(const item of value){
      if(item.sex===sex){
        returnArray.push(item)
      }
    }
    return returnArray
  }

}
